import {Attribute, ChangeDetectionStrategy, Component, Directive, ElementRef, InjectionToken, Input, ViewEncapsulation} from '@angular/core';

let nextUniqueId = 0;

/**
 * Injection token that can be used to reference instances of `TDSError`. It serves as
 * alternative token to the actual `TDSError` class which could cause unnecessary
 * retention of the class and its directive metadata.
 */
export const TDS_WARNING = new InjectionToken<TDSWarning>('TDSWarning');

/** Single success message to be shown underneath the form field. */
@Component({
  selector: 'tds-warning',
  template:`
    <i class='mr-2' [ngClass]="icon"> 
    </i>
    <ng-content></ng-content>
  `,
  host: {
    'class': 'tds-warning text-warning-500 text-xs leading-4 font-normal mt-1 inline-flex items-center',
    '[attr.id]': 'id',
    'aria-atomic': 'true',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [{provide: TDS_WARNING, useExisting: TDSWarning}],
})
export class TDSWarning {
  @Input() id: string = `tds-warning-${nextUniqueId++}`;
  @Input() icon:string = 'tdsi-warning-fill text-base leading-4';
  constructor(@Attribute('aria-live') ariaLive: string, elementRef: ElementRef) {
    // If no aria-live value is set add 'polite' as a default. This is preferred over setting
    // role='alert' so that screen readers do not interrupt the current task to read this aloud.
    if (!ariaLive) {
      elementRef.nativeElement.setAttribute('aria-live', 'polite');
    }
  }
}
