import { Platform } from "@angular/cdk/platform";
import { AfterViewInit, ChangeDetectorRef, ContentChild, ContentChildren, Inject, InjectionToken, Input, NgZone, QueryList, ViewChild, ViewEncapsulation } from "@angular/core";
import { AfterContentInit } from "@angular/core";
import { OnDestroy } from "@angular/core";
import { AfterContentChecked } from "@angular/core";
import { Component, ChangeDetectionStrategy, ElementRef } from "@angular/core";
import { AbstractControl, NgControl } from "@angular/forms";
import { fromEvent, merge, Subject } from "rxjs";
import { startWith, take, takeUntil } from "rxjs/operators";
import { ButtonSize } from "tds-ui/core/config";
import { TDSHelperArray } from "tds-ui/shared/utility";

import { TDSAddOnLeft, TDSAddOnRight, TDS_ADDONLEFT, TA_ADDONRIGHT } from "./add-on";
import { TDSError, TDS_ERROR } from "./error";
import { tdsFormFieldAnimations } from "./form-field-animations";
import { CanInputColor, CanInputColorCtor, InputColor, mixinInputColor } from "./form-field-color";
import { TDSFormFieldControl } from "./form-field-control";
import { TDSLabel } from "./label";
import { TDSPrefix, TDS_PREFIX } from "./prefix";
import { TDSSuffix, TDS_SUFFIX } from "./suffix";
let nextUniqueId = 0;

/**
 * Boilerplate for applying mixins to TDSFormField.
 * @docs-private
 */
class TAFormFieldBase {

    constructor(public _elementRef: ElementRef) { }
}
/**
* Base class to which we're applying the form field mixins.
* @docs-private
*/
const _TAFormFieldMixinBase: CanInputColorCtor & typeof TAFormFieldBase =
    mixinInputColor(TAFormFieldBase, 'default');


export const TDS_FORM_FIELD = new InjectionToken<TDSFormField>('TDSFormField');

@Component({
    selector: 'tds-form-field',
    exportAs: 'taFormField',
    templateUrl: './form-field.html',
    host: {
        'class': 'block relative text-left',
        '[class.tds-form-field-invalid]': '_control.errorState',
        '[class.tds-form-field-disabled]': '_control.disabled',
        '[class.tds-form-field-autofilled]': '_control.autofilled',
        '[class.ta-focused]': '_control.focused',
        '[class.ng-untouched]': '_shouldForward("untouched")',
        '[class.ng-touched]': '_shouldForward("touched")',
        '[class.ng-pristine]': '_shouldForward("pristine")',
        '[class.ng-dirty]': '_shouldForward("dirty")',
        '[class.ng-valid]': '_shouldForward("valid")',
        '[class.ng-invalid]': '_shouldForward("invalid")',
        '[class.ng-pending]': '_shouldForward("pending")',
    },
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    providers: [
        { provide: TDS_FORM_FIELD, useExisting: TDSFormField },
    ],
    animations: [tdsFormFieldAnimations.transitionMessages],
})

export class TDSFormField
    implements AfterContentInit, AfterContentChecked, AfterViewInit, OnDestroy {
    private readonly _destroyed = new Subject<void>();
    private _color: InputColor = "default";
    private _size: ButtonSize = "md";
    private _tdsClass: string = 'dark:bg-d-neutral-3-50 border bg-white ';
    private _tdsClassDefault = 'dark:border-d-neutral-2-200 border-neutral-2-200';
    clsContainer: string = '';
    @Input() tdsClass: string = '';

    @Input()
    get color() {
        return this._color
    }
    set color(val: InputColor) {
        this._color = val;
    }
    // @Input()
    get size() {
        return (this._control && this._control.size) ? this._control.size : this._size;
    }

    readonly _labelId = `tds-form-field-label-${nextUniqueId++}`;
    @ViewChild('connectionContainer', { static: true }) _connectionContainerRef!: ElementRef;
    @ViewChild('inputContainer') _inputContainerRef!: ElementRef;

    @ContentChild(TDSFormFieldControl) _controlNonStatic!: TDSFormFieldControl<any>;
    @ContentChild(TDSFormFieldControl, { static: true }) _controlStatic!: TDSFormFieldControl<any>;

    get _control() {

        // TODO(crisbeto): we need this workaround in order to support both Ivy and ViewEngine.
        //  We should clean this up once Ivy is the default renderer.
        return this._explicitFormFieldControl || this._controlNonStatic || this._controlStatic;
    }
    set _control(value) {

        this._explicitFormFieldControl = value;
    }
    private _explicitFormFieldControl!: TDSFormFieldControl<any>;

    @ContentChild(TDSLabel) _labelChildNonStatic!: TDSLabel;
    @ContentChild(TDSLabel, { static: true }) _labelChildStatic!: TDSLabel;
    @ContentChildren(TDSLabel, { descendants: false }) _labelChildChildren!: QueryList<TDSLabel>;
    @ContentChildren(TDS_ERROR, { descendants: true }) _errorChildren!: QueryList<TDSError>;
    // @ContentChildren(_MAT_HINT, { descendants: true }) _hintChildren: QueryList<MatHint>;
    @ContentChildren(TDS_PREFIX, { descendants: true }) _prefixChildren!: QueryList<TDSPrefix>;
    @ContentChildren(TDS_SUFFIX, { descendants: true }) _suffixChildren!: QueryList<TDSSuffix>;
    @ContentChildren(TDS_ADDONLEFT, { descendants: true }) _addOnLeftChildren!: QueryList<TDSAddOnLeft>;
    @ContentChildren(TA_ADDONRIGHT, { descendants: true }) _addOnRightChildren!: QueryList<TDSAddOnRight>;
    constructor(
        public _elementRef: ElementRef, private _changeDetectorRef: ChangeDetectorRef,

    ) {


    }
    /**
  * Gets an ElementRef for the element that a overlay attached to the form-field should be
  * positioned relative to.
  */
    getConnectedOverlayOrigin(): ElementRef {
        return this._connectionContainerRef || this._elementRef;
    }

    ngAfterContentInit() {

        this._validateControlChild();

        const control = this._control;
        if (control.controlType) {
            this._elementRef.nativeElement.classList.add(`tds-form-field-type-${control.controlType}`);
        }

        // Subscribe to changes in the child control state in order to update the form field UI.
        control.stateChanges.pipe(startWith(null)).subscribe(() => {
            this.updateCssContainer();
            this._syncDescribedByIds();
            this._changeDetectorRef.markForCheck();
        });

        // Run change detection if the value changes.
        if (control.ngControl && control.ngControl.valueChanges) {
            control.ngControl.valueChanges
                .pipe(takeUntil(this._destroyed))
                .subscribe(() => {
                    this.updateCssContainer();
                    this._changeDetectorRef.markForCheck()
                }
                );
        }

        // Run change detection and update the outline if the suffix or prefix changes.
        merge(this._prefixChildren.changes, this._suffixChildren.changes).subscribe(() => {
            this._changeDetectorRef.markForCheck();
        });
        // Update the aria-described by when the number of errors changes.
        this._errorChildren.changes.pipe(startWith(null)).subscribe(() => {
            this._syncDescribedByIds();
            this.updateCssContainer();
            this._changeDetectorRef.markForCheck();
        });
    }

    ngAfterContentChecked() {
        this._validateControlChild();
    }

    ngAfterViewInit() {
        
        if (TDSHelperArray.hasListValue(this._labelChildChildren) && this.hasRequired)
            this._labelChildChildren.forEach(el => {                
                el.updateRequired(this.hasRequired);
            })
        this._changeDetectorRef.detectChanges();
    }

    ngOnDestroy() {
        this._destroyed.next();
        this._destroyed.complete();
    }
    /**
      * Gets the id of the label element. If no label is present, returns `null`.
      */
    getLabelId(): string | null {
        return this._hasLabel() ? this._labelId : null;
    }

    /** Determines whether a class from the NgControl should be forwarded to the host element. */
    _shouldForward(prop: keyof NgControl): boolean {
        const ngControl = this._control ? this._control.ngControl : null;
        return ngControl && ngControl[prop];
    }

    _hasPlaceholder() {
        return !!(this._control && this._control.placeholder);
    }

    _hasLabel() {
        return !!(this._labelChildNonStatic || this._labelChildStatic);
    }

    /** Determines whether to display hints or errors. */
    _getDisplayedMessages(): string | undefined {
        return (this._control.errorState) ? 'error' : this.color;
    }

    _hasAddOnRightChildren() {
        return this._addOnRightChildren && this._addOnRightChildren.length > 0;
    }
    _hasAddOnLeftChildren() {
        return this._addOnLeftChildren && this._addOnLeftChildren.length > 0;
    }
    _hasSuffixChildren() {
        return this._suffixChildren && this._suffixChildren.length > 0;
    }
    _hasPrefixChildren() {
        return this._prefixChildren && this._prefixChildren.length > 0;
    }
    get hasRequired() {
        return (this._control.required || this.hasRequiredValidator(this._control.ngControl?.control)) && !this._control.disabled
    }
    hasRequiredValidator(control: AbstractControl | null | undefined): boolean {
        if (control?.validator) {
            const validator = control?.validator({} as AbstractControl);
            if (validator && validator.required) {
                return true;
            }
        }
        return false;
    }
    /**
     * Sets the list of element IDs that describe the child control. This allows the control to update
     * its `aria-describedby` attribute accordingly.
     */
    private _syncDescribedByIds() {
        if (this._control) {
            let ids: string[] = [];

            // TODO(wagnermaciel): Remove the type check when we find the root cause of this bug.
            if (this._control.userAriaDescribedBy &&
                typeof this._control.userAriaDescribedBy === 'string') {
                ids.push(...this._control.userAriaDescribedBy.split(' '));
            }

            if (this._errorChildren) {
                ids.push(...this._errorChildren.map(error => error.id));
            }

            this._control.setDescribedByIds(ids);
        }
    }

    /** Throws an error if the form field's control is missing. */
    protected _validateControlChild() {
        if (!this._control) {
            throw 'tds-form-field must contain a TDSFormFieldControl.';
        }
    }

    private updateCssContainer() {
        // console.log('updateCssContainer');
        let cls = this._tdsClass;
        if(this._control.disabled)
        {
            cls ='dark:bg-d-neutral-3-50 border bg-neutral-3-50 cursor-not-allowed'
        }else
        if (this._control.focused) {
            cls += ` ${this.p_ClassFocus()}`
        } else if (this._control.errorState) {
            cls += ` outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-error-400 dark:ring-d-error-400 border-error-400`
        }
        else {
            if (this.color == "default" || this.color == 'dark')
                cls += ` ${this._tdsClassDefault} `
            else {
                cls += this.p_getClassBorderByColor(this.color);
            }
        }
        this.clsContainer = cls + ` ${this.p_GetBorderRadius()}` + ` ${this.tdsClass}`;
    }
    private p_GetBorderRadius() {
        let rounded = 'rounded';
        if (this._addOnLeftChildren.length > 0) {
            rounded = "rounded-r";
        } else if (
            this._addOnRightChildren.length > 0
        ) {
            rounded = 'rounded-l';
        }
        return rounded;
    }
    private p_ClassFocus() {
        let result = '';
        if (this.color == "default" || this.color == 'dark') {
            result = 'outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-primary-1 border-primary-1 dark:ring-d-primary-1 dark:border-d-primary-1';
        } else {
            result = this.p_getClassFocusByColor(this.color);
        }
        return result;
    }

    private p_getClassBorderByColor(color: InputColor) {
        let result = '';
        switch (color) {
            case "dark":
                result = `border-transparent`,
                    result = 'outline-none ring-opacity-20 dark:ring-opacity-20 ring  ring-primary-1 dark:ring-d-primary-1 border-primary-1 dark:border-d-primary-1';
                break;
            case "success":
                result = `border-success-400 dark:border-d-success-400`,
                    result = `outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-success-400 dark:ring-d-success-400 border-success-400 dark:border-d-success-400`;
                break;
            case "error":
                result = `border-error-400 dark:border-d-error-400`,
                    result = `outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-error-400 dark:ring-d-error-400 border-error-400 dark:border-d-error-400`;
                break;
            case "warning":
                result = `border-warning-400 dark:border-d-warning-400`,
                    result = `outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-warning-400 dark:ring-d-warning-400 border-warning-400 dark:border-d-warning-400`;
                break;
            default:
                result = 'border-neutral-2-200 dark:border-d-neutral-2-200',
                    result = 'outline-none ring-opacity-20 dark:ring-opacity-20 ring  ring-primary-1 dark:ring-d-primary-1 border-primary-1 dark:border-d-primary-1';
                break;

        }
        return result;
    }
    private p_getClassFocusByColor(color: InputColor) {
        let result = '';
        switch (color) {
            case "dark":

                result = 'outline-none ring-opacity-20 dark:ring-opacity-20 ring  ring-primary-1 dark:ring-d-primary-1 border-primary-1 dark:border-d-primary-1';
                break;
            case "success":

                result = `outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-success-400 dark:ring-d-success-400 border-success-400 dark:border-d-success-400`;
                break;
            case "error":

                result = `outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-error-400 dark:ring-d-error-400 border-error-400 dark:border-d-error-400`;
                break;
            case "warning":

                result = `outline-none ring-opacity-20 dark:ring-opacity-20 ring ring-warning-400 dark:ring-d-warning-400 border-warning-400 dark:border-d-warning-400`;
                break;
            default:

                result = 'outline-none ring-opacity-20 dark:ring-opacity-20 ring  ring-primary-1 dark:ring-d-primary-1 border-primary-1 dark:border-d-primary-1';
                break;

        }
        return result;
    }
}