import {Attribute, ChangeDetectionStrategy, Component, Directive, ElementRef, InjectionToken, Input, ViewEncapsulation} from '@angular/core';

let nextUniqueId = 0;

/**
 * Injection token that can be used to reference instances of `TDSError`. It serves as
 * alternative token to the actual `TDSError` class which could cause unnecessary
 * retention of the class and its directive metadata.
 */
export const TDS_ERROR = new InjectionToken<TDSError>('TDSError');

/** Single error message to be shown underneath the form field. */
@Component({
  selector: 'tds-error',
  template:`
    <i class='mr-2' [ngClass]="icon"> 
    </i>
    <ng-content></ng-content>
  `,
  host: {
    'class': 'tds-error text-xs leading-4 font-normal text-error-500  dark:text-d-error-500 mt-1 inline-flex items-center',
    '[attr.id]': 'id',
    'aria-atomic': 'true',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [{provide: TDS_ERROR, useExisting: TDSError}],
})
export class TDSError {
  @Input() id: string = `tds-error-${nextUniqueId++}`;
  @Input() icon:string = 'tdsi-warning-2-fill text-base leading-4';
  constructor(@Attribute('aria-live') ariaLive: string, elementRef: ElementRef) {
    if (!ariaLive) {
      elementRef.nativeElement.setAttribute('aria-live', 'polite');
    }
  }
}
