import { Directive, ElementRef, InjectionToken, Input } from '@angular/core';

import { ButtonSize } from "tds-ui/core/config";
import { Constructor, HasElementRef } from 'tds-ui/shared/common';

/**
 * Injection token that can be used to reference instances of `TDSAddOnLeft`. It serves as
 * alternative token to the actual `TDSAddOnLeft` class which could cause unnecessary
 * retention of the class and its directive metadata.
 */
export const TDS_ADDONLEFT = new InjectionToken<TDSAddOnLeft>('TDSAddOnLeft');



export interface CanAddOnSize {
  defaultSize: ButtonSize;
  size: ButtonSize;

}

/** @docs-private */
export type CanAddOnSizeCtor = Constructor<CanAddOnSize>;
export function mixinAddOnSize<T extends Constructor<HasElementRef>>(
  base: T, defaultSize?: ButtonSize): CanAddOnSizeCtor & T {
  return class extends base {
    private _size: ButtonSize;
    defaultSize = defaultSize;

    get size(): ButtonSize { return this._size; }
    set size(value: ButtonSize) {
      const _size = value || this.defaultSize;
      if (_size !== this._size) {
        if (this._size) {
          const lstClass = this.getClassbySize(this._size);
          lstClass.forEach(cl => {
            this._elementRef.nativeElement.classList.remove(cl);
          });
        }
        if (_size) {
          const lstClass = this.getClassbySize(_size);
          lstClass.forEach(cl => {
            this._elementRef.nativeElement.classList.add(cl);
          });
        }
        this._size = defaultSize;
      }
    }

    constructor(...args: any[]) {
      super(...args);
      // Set the default color that can be specified from the mixin.
      this.size = defaultSize;


    }
    getClassbySize(size: ButtonSize): Array<string> {
      let result: Array<string> = [];
      switch (size) {
        case "sm":
          result = ['p-1']
          break;
        case "md":
          result = ['p-1.5',]
          break;
        case "lg":
          result = ['p-2',]
          break;

      }
      return result;
    }
  };
}

class TDSAddOnBase {
  constructor(public _elementRef: ElementRef) { }
}

const _TDSAddOnMixinBase: CanAddOnSizeCtor &
  typeof TDSAddOnBase = mixinAddOnSize(TDSAddOnBase, 'md');

/** AddOnLeft to be placed in front of the form field. */
@Directive({
  selector: '[tdsAddOnLeft]',
  providers: [{ provide: TDS_ADDONLEFT, useExisting: TDSAddOnLeft }],
  host: {
    class: "h-full border-neutral-2-200 dark:border-d-neutral-2-200 border rounded-l border-r-0 flex",    
  },
  inputs: ['size']
})
export class TDSAddOnLeft extends _TDSAddOnMixinBase
  implements CanAddOnSize {

  constructor(elementRef: ElementRef
  ) {
    super(elementRef);
  }
}

export const TA_ADDONRIGHT = new InjectionToken<TDSAddOnLeft>('TDSAddOnRight');
/** AddOnLeft to be placed in front of the form field. */
@Directive({
  selector: '[tdsAddOnRight]',
  providers: [{ provide: TA_ADDONRIGHT, useExisting: TDSAddOnRight }],
  host: {
    class: "h-full border-neutral-2-200 dark:border-d-neutral-2-200 border rounded-r border-l-0 flex"
  },
  inputs: ['size']
})
export class TDSAddOnRight extends _TDSAddOnMixinBase
implements CanAddOnSize {

constructor(elementRef: ElementRef
) {
  super(elementRef);
}
}
