import {  Directive, ElementRef, Input } from "@angular/core";
import { InputColor } from "./form-field-color";

@Directive({
    selector: `[tds-form-field-container]`, 
    host:{
        "[class]":'tdsClass'
    }
})
export class TDSFormFieldContainer{
    private _color: InputColor = "default";
    private _focus: boolean = false;
    private _disabled: boolean = false;
    defaultColor: InputColor = "default";
    private _tdsClass = "";
    @Input()
    get tdsClass() {
        return this._tdsClass
    }
    set tdsClass(value: string) {
        this._tdsClass = value;
    }
    constructor(private _elementRef: ElementRef) {
    }
}